import Vue from 'vue'
import Vuex from 'vuex'
import ls from '../utils/localStorage'
import router from '../router'
//  actions.js 
import * as moreActions from './actions'
import * as moreGetters from './getters'

Vue.use(Vuex)

const state = {
  //  localStorage 
  user: ls.getItem('user'),
  //  auth 
  auth: ls.getItem('auth'),
  //  articles 
  articles: ls.getItem('articles'),
  // 
  searchValue: '',
  //  location.origin
  origin: location.origin.indexOf('github.io') !== -1 ? `${location.origin}/vuejs-essential/dist` : location.origin
}

// 
const mutations = {
  // state state user   
  UPDATE_USER(state, user) {
    // user
    state.user = user
    // 
    ls.setItem('user', user)
  },
  //  UPDATE_AUTH 
  UPDATE_AUTH(state, auth) {
    state.auth = auth
    ls.setItem('auth', auth)
  },
  // 
  UPDATE_ARTICLES(state, articles) {
    state.articles = articles
    ls.setItem('articles', articles)
  },
   // 
   UPDATE_SEARCH_VALUE(state, searchValue) {
    state.searchValue = searchValue
  }
}

// 
const actions = {
  // {commit = context.commit}  action  context  context.state  state context.commit  { commit }
  login({ commit }, user) {
    //    
    if (user) commit('UPDATE_USER', user)

    //  
    commit('UPDATE_AUTH',true)

    //  router.push({ path: '/' })
    //  router.push({ name: 'Home' })
    //  router.push({ params: { id: 1 }, query: { page: 1 } })
    //  //  router.push('/')  
    router.push('/')
  },
  logout({ commit }) {
     //  
     commit('UPDATE_AUTH',false)
     router.push({ name: 'Home' ,params: { logout: true }})
  },
  //  mutations  UPDATE_USER  mutations 
  updateUser({ state, commit }, user) {
    // 
    const stateUser = state.user
  
    // 
    if (stateUser && typeof stateUser === 'object') {
      //  user = Object.assign({}, stateUser, user)
      user = { ...stateUser, ...user }
    }
  
    // 
    commit('UPDATE_USER', user)
  },
  //  moreActions  const actions = Object.assign(actions, moreActions)
  ...moreActions
}

//  state  Getter store.getters.getArticleById Getter  state getters Getter 
const getters = {
  //  state id
  getArticleById: (state, getters) => (id) => {
    // 
    // let articles = state.articles
    //  computedArticles 
    let articles = getters.computedArticles

    // 
    if(Array.isArray(articles)) {
      //  id  articleId 
      articles = articles.filter(article => parseInt(id) === parseInt(article.articleId))
      //  null
      return articles.length ? articles[0] : null
    }else {
      return null
    }
  },

  //  moreGetters,  getters = Object.assign(getters, moreGetters)
  ...moreGetters
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})

export default store