<template>
  <!-- v-show  CSS  display show  true  -->
  <div v-show="show" :class="`alert alert-${type} alert-dismissible`">
    <button @click="close" type="button" class="close"><span></span></button>
    {{ msg }}
  </div>
</template>

<script>
export default {
  name: 'Message',
  // props  props  3  type  default 
  // <Message :show.sync="msgShow" :type="msgType" :msg="msg"/>Copy
  // type 
  // String Number Boolean Function Object Array Symbol
  props: {
    // 
    show: {
      type: Boolean,
      default: false
    },
    // 
    type: {
      type: String,
      default: 'success'
    },
    // 
    msg: {
      type: String,
      default: ''
    }
  },
  // watch  show  value show(val, oldVal)val oldVal  true 
  //  $nextTick  DOM  Vue.nextTick  
  watch: {
    show(value) {
      if (value) {
        this.$nextTick(() => {
          this.$el.scrollIntoView(true)
        })
      }
    }
  },
  methods: {
    close() {
      // $emit  'update:show'  false  
      this.$emit('update:show', false)
    }
  }
}
</script>

