//  Message 
import MessageComponent from '../components/Message'

export default {
  //  install
  install: (Vue) => {
    // Vue.extend  Message 
    const Message = Vue.extend(MessageComponent)
    //  new  Message  el 
    const vm = new Message()
    //  vm.$mount()  $el
    const $el = vm.$mount().$el

    Vue.nextTick(() => {
      //  DOM  #main-container 
      document.querySelector('#main-container').prepend($el)
    })

    // $on  $on  Message 
    vm.$on('update:show', (value) => {
      //  show 
      vm.show = value
    })

    //  show  hide 
    const message = {
      //   showMsg   
      show(msg = '', type = 'success') {
        vm.msg = msg
        vm.type = type
        vm.show = false

        Vue.nextTick(() => {
          vm.show = true
        })
      },
      // 
      hide() {
        Vue.nextTick(() => {
          vm.show = false
        })
      }
    }

    // 
    Vue.prototype.$message = message
  }
}