// mode 'hash'  #  'history'  History API 
const router = new Router({
  mode: 'history',
  // linkExactActiveClass  <router-link>  <router-link> 'router-link-exact-active' 'active'  Bootstrap 
  linkExactActiveClass: 'active',
  // 
  //  scrollBehavior  to  from  savedPosition / 
  // scrollBehavior  history.pushState history.pushState 
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      // 
      return { selector: to.hash }
    } else if (savedPosition) {
      // 
      return savedPosition
    } else {
      // 
      return { x: 0, y: 0 }
    }
  },
  routes
})

//   
router.beforeEach( (to,from,next) => {
  // 
  //  import store from '../store'   const auth = store.state.auth
  //  $options  Vue 
  const app = router.app
  const store = app.$options.store
  const auth = store.state.auth
  // articleId
  const articleId = to.params.articleId
  // 
  const user = store.state.user && store.state.user.name
  // 
  const paramUser = to.params.user

  app.$message.hide()
  if ((auth && to.path.indexOf('/auth/') !== -1) || (!auth && to.meta.auth ) || (articleId && !store.getters.getArticleById(articleId)) || (paramUser && paramUser !== user && !store.getters.getArticlesByUid(null, paramUser).length)) {
    //  /auth/     Id   
    next('/')
  } else {
    next()
  }
})

//  router.afterEach  next 
router.afterEach( (to, from) => {
  // 
  const app = router.app
  const store = app.$options.store
  const showMsg = to.params.showMsg

  if(showMsg) {
    if(typeof showMsg === 'string') {
      app.$message.show(showMsg)
    }else {
      app.$message.show('')
    }
  }
})

export default router