<template>
  <div class="navbar-form navbar-left hidden-sm">
    <div class="form-group">
      <!-- .enter  
      .enter
      .tab
      .delete
      .esc
      .space
      .up
      .down
      .left
      .right -->
      <!--  v-model  searchValue input  updateSearchValue -->
      <input
         v-model.trim="searchValue"
        type="text"
        class="form-control search-input mac-style"
        placeholder=""
        @keyup.enter="search"
        @input="updateSearchValue"
      >
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchInput',
  data() {
    return {
      value: ''
    }
  },
  // 
  computed: {
    searchValue: {
      get() { // getter
        return this.$store.state.searchValue
      },
      //  getter setter  this.$store.state.searchValue  newValue 
      set(newValue) { // setter
        this.value = newValue
      }
    }
  },
  methods: {
    search() {
      const value = this.value
      if (value !== '' && value !== this.$route.query.q) {
        //  q 
        this.$router.push({ name: 'Search', query: { q: value } })
      }
    },
    //  searchValue
    updateSearchValue() {
      this.$store.commit('UPDATE_SEARCH_VALUE', this.value)
    }
  }
}
</script>

<style scoped>
.search-input { background-image: url(https://cdn.learnku.com/assets/images/icon-search.png)}
</style>